.quiz-body {
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,51,121,1) 0%, rgba(0,212,255,1) 100%);
    height: 100vh;
    display: flex;
    justify-content: center;
  }
  
  .quiz-container {
    max-width: 500px;
    min-width: 250px;
    background: #ffffff;
    border-radius: 4px;
    margin-top: 100px;
    padding: 30px 60px;
    height: 500px;
  }
  
  .quiz-container .active-question-no {
    font-size: 32px;
    font-weight: 500;
    color: #0000FF;
  }
  
  .quiz-container .total-question {
    font-size: 16px;
    font-weight: 500;
    color: #e0dee3;
  }
  
  .quiz-container h2 {
    font-size: 20px;
    font-weight: 500;
    margin: 0;
  }
  
  .quiz-container ul {
    margin-top: 20px;
    margin-left: -40px;
  }
  
  .quiz-container ul li {
    text-decoration: none;
    list-style: none;
    color: black;
    font-size: 16px;
    background: #ffffff;
    border: 1px solid #eaeaea;
    border-radius: 16px;
    padding: 11px;
    margin-top: 15px;
    cursor: pointer;
  }
  
  .quiz-container ul .selected-answer {
    background: #00FFFF;
    border: 1px solid #0000FF;
  }
  
  .quiz-container button {
    background: linear-gradient(90.04deg, #0080FF 0.03%, #66FFFF 99.96%);
    border-radius: 9px;
    font-size: 18px;
    color: #ffffff;
    padding: 10px 42px;
    outline: none;
    border: none;
    cursor: pointer;
    margin-top: 15px;
  }
  
  .quiz-container button:disabled {
    background: #e7e8e9;
    color: #9fa3a9;
    cursor: not-allowed;
  }
  
  .flex-right {
    display: flex;
    justify-content: flex-end;
  }
  
  .result h3 {
    font-size: 24px;
    letter-spacing: 1.4px;
    text-align: center;
  }
  
  .result p {
    font-size: 16px;
    font-weight: 500;
  }
  
  .result p span {
    color: #0000FF;
    font-size: 22px;
  }